'use server';
/**
 * @fileOverview Generates AI-powered insights about the portfolio.
 *
 * - portfolioInsights - A function that generates insights based on the portfolio's content.
 * - PortfolioInsightsInput - The input type for the portfolioInsights function.
 * - PortfolioInsightsOutput - The return type for the portfolioInsights function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const PortfolioInsightsInputSchema = z.object({});
export type PortfolioInsightsInput = z.infer<typeof PortfolioInsightsInputSchema>;

const PortfolioInsightsOutputSchema = z.object({
  summary: z.string().describe('A summary of the key principles of the portfolio, based on the provided philosophy and content.'),
});
export type PortfolioInsightsOutput = z.infer<typeof PortfolioInsightsOutputSchema>;

const PHILOSOPHY = "People don't pay for products – they pay for outcomes. Validated proof-of-work is the ultimate career accelerator.";

const PORTFOLIO_CONTEXT = `
- **Role**: Civil Engineer & Tech Innovator
- **Focus**: Blending civil engineering with digital solutions, helping professionals build their careers.
- **Key Innovations**: Proof-of-Work Framework, Talent Discovery Models, Assignment-based Hiring.
- **Experience**: Multidisciplinary Freelancer (Design, Home Planning, Digital Products), Site Engineer, Campus Ambassador.
- **Skills**: Civil Engineering (AutoCAD, Revit), Web Development (React, Python, SaaS), Professional Skills (Leadership, Project Management, Marketing).
`;

export async function portfolioInsights(input: PortfolioInsightsInput): Promise<PortfolioInsightsOutput> {
  return portfolioInsightsFlow(input);
}

const prompt = ai.definePrompt({
  name: 'portfolioInsightsPrompt',
  input: {schema: PortfolioInsightsInputSchema},
  output: {schema: PortfolioInsightsOutputSchema},
  prompt: `You are a professional brand strategist. Your task is to analyze the provided portfolio information and generate a concise, insightful summary of the individual's core principles.

  **Core Philosophy to guide your analysis:**
  "${PHILOSOPHY}"

  **Portfolio Context:**
  ${PORTFOLIO_CONTEXT}

  Based on the philosophy and context, generate a summary that highlights the key principles driving this person's work. The tone should be professional and inspiring. Focus on the theme of bridging practical skills with real-world outcomes.
  `,
});

const portfolioInsightsFlow = ai.defineFlow(
  {
    name: 'portfolioInsightsFlow',
    inputSchema: PortfolioInsightsInputSchema,
    outputSchema: PortfolioInsightsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
